.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 20px;
}

.gallery-item {
  background: #6c82d1;
  border: 1px solid #6c82d1;
  border-radius: 8px;
  // overflow: scroll;
  padding: 10px;
  text-align: center;
  width: calc(20% - 20px);
  max-width: 300px;
  cursor: pointer;
}

.gallery-item img {
  border-bottom: 1px solid #6c82d1;
  max-width: 50%;
  height: auto;
  margin-bottom: 10px;
}

.gallery-item p {
  margin: 0;
  font-size: 14px;
  color: #333;
  max-width: 90%;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: #6c82d1;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  position: relative;
  max-width: 70%;
}

.modal-content img {
  max-width: 70%;
  height: 70%;
}

.modal-content p {
  margin-top: 10px;
  font-size: 16px;
  color: #333;
  max-width: 70%;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
@media only screen and (max-width: 850px) {
  .gallery {
    flex-direction: column;
    align-items: center;
  }
  .gallery-item {
    background: #6c82d1;
    border: 1px solid #6c82d1;
    border-radius: 8px;
    overflow: hidden;
    padding: 10px;
    text-align: center;
    width: calc(90% - 20px);
    max-width: 300px;
    cursor: pointer;
  }

  .gallery-item img {
    border-bottom: 1px solid #6c82d1;
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
  }

  .gallery-item p {
    margin: 0;
    font-size: 11px;
    color: #333;
  }

  .modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }

  .modal-content {
    background: #6c82d1;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    position: relative;
  }

  .modal-content img {
    max-width: 100%;
    height: auto;
  }

  .modal-content p {
    margin-top: 10px;
    font-size: 10px;
    color: #333;
  }
}
