@import url("https://fonts.googleapis.com/css2?family=Poetsen+One&display=swap");
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
@import "../css/global.scss";

:root {
  --background-color: #030014;
  --background-light: #fff;
  --shadow-colour: rgba(0, 0, 0, 0.2);
  --primary-color: rgb(183, 0, 255);
  --text-color: #fff;
  --text-light: #575757;
  --font-size: 16px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poetsen One", sans-serif;
}
body {
  background-color: var(--background-color);
}

.heros {
  height: calc(100% - 100px);
  overflow: hidden;
  position: relative;
  z-index: -10;
  .wrappers {
    max-width: 1100px;
    height: 100%;
    margin: auto;
    overflow-y: auto;
    padding: 20px;
    box-sizing: border-box;
    z-index: 20;
    text-align: center;
    .headone {
      font-size: 30px;
      color: rebeccapurple;
      letter-spacing: 10px;
      font-weight: 800;
    }

    .textContainers {
      color: #b08eff;
      width: 100%;
      height: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: justify;
      gap: 40px;
      @include tablet {
        width: 100%;
        align-items: center;
      }

      h2 {
        font-size: 30px;
        color: rebeccapurple;
        letter-spacing: 10px;
        font-weight: 800;
      }
      h1 {
        font-size: 88px;
        font-style: normal;
        background: linear-gradient(0deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4)),
          linear-gradient(90.01deg, #e59cff 0.01%, #ba9cff 50.01%, #6c82d1 100%);
        background-blend-mode: normal, screen;
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        @include tablet {
          font-size: 36px;
        }
      }
      .buttonss {
        display: flex;
        justify-content: center;
        align-items: center;
        button {
          padding: 20px;
          border: none;
          border-radius: 10px;
          background-color: transparent;
          color: whitesmoke;
          margin-right: 20px;
          cursor: pointer;
          font-weight: 300;
        }
      }
      img .netArt {
        height: 80%;
        width: 80%;
        text-align: center;
      }
      .scrollBtn {
        margin: auto;
      }
    }
  }

  .imageContainerr {
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
  }

  .slidingTextContainers {
    position: absolute;
    font-size: 40vh;
    bottom: -120px;
    white-space: nowrap;
    width: 80%;
    font-weight: bold;
    color: #b748ee17;
    z-index: -599;
  }
}
